.new-minecraft{
  .for-pupils {
          padding: 20px 0;
          text-align: center;
          @media screen and (min-width: 768px) {
              padding: 40px 0;
            }
            @media screen and (min-width: 1280px) {
              padding: 60px 0;
            }
             .text{
              font-size: 16px;
              margin-bottom: 42px;
              @media screen and (min-width: 768px) {
                  font-size: 20px;
                  margin-bottom: 40px;
                }
            }
  }
  .for-pupils_list {
      display: flex;
      flex-direction: column;
      gap:12px;
      margin-bottom: 40px;
      @media screen and (min-width: 768px) {
          flex-direction: row;
          flex-wrap: wrap;
          justify-content: center;
          row-gap: 20px;
          column-gap: 32px;
        }
        @media screen and (min-width: 1280px) {
          justify-content: center;
          row-gap: 40px;
          column-gap: 32px;
          margin-bottom: 60px;
        }
  }
  .for-pupils_item {
      display: flex;
      align-items: center;
      flex-direction: column;
      gap:16px;
      padding: 16px 8px 8px;
      box-shadow: 0px 1px 2px rgba(2, 105, 0, 0.08), 10px 10px 32px rgba(2, 105, 0, 0.14);
border-radius: 4px;
@media screen and (min-width: 768px) {
 flex-basis: calc((100% - 32px) / 2);
}
@media screen and (min-width: 1280px) {
  flex-basis: calc((100% - 2 * 32px) / 3);
}
& > h3{
  font-size: 20px;
  line-height: 1.4;
  font-weight: 500;
  @media screen and (min-width: 768px) {
      font-size: 22px;
      line-height: 1.4;
    }

}
& > p{
  font-size: 16px;
  line-height: 1.5;
  @media screen and (min-width: 768px) {
      font-size: 20px;
    }
}
  }
  .for-pupils_info-block {
    width: 100%;
      background-color: $color-nm-yellow;
      display: flex;

      justify-content: center;
      flex-direction: column;
      padding: 20px 12px;
      gap: 20px;
      border-radius: 4px;
      @media screen and (min-width: 768px) {
          flex-direction: row;
          padding: 20px 32px;
          align-items: center;
          gap:40px;
      }
      @media screen and (min-width: 1280px) {
      padding: 20px 59px;
      max-width: 1008px;
      margin: 0 auto;
      gap: 60px;
  }
&>svg{
  min-width: 130px;
  height: 70px;
}
  & > p{
    text-align: left;
      font-size: 16px;
      line-height: 1.75;
      
      & > span{
          font-weight: 700;

      }
      @media screen and (min-width: 768px) {
          font-size: 20px;
          line-height: 1.8;
      }
  }
}
}